import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { getChartDocIns, getChartDocOuts } from 'services/chart';
import PieChartService from './PieChartService';

class DashboardDoc extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        <Col span={12}>
          <PieChartService chartName="Công văn đến" service={getChartDocIns} />
        </Col>
        <Col span={12}>
          <PieChartService chartName="Công văn đi" service={getChartDocOuts} />
        </Col>
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardDoc {...props} />
  </Page>
);



